.portfolio-container{
	width: 100%;
	max-width: 1180px;

	display: flex;
	flex-direction: column;
	align-items: center;

	padding: 0 20px;
	margin: 0 auto;
}

.portfolio-list {
	width: 100%;

	display: flex;
	flex-wrap:wrap;
	align-items: flex-start;

}

.portfolio-item {
	width: 100%;
	max-width: 360px;
	min-height: 325px;

	margin-right: 20px;
	margin-bottom: 20px;

	text-align: left;

	&:nth-child(3n){
		margin-right: 0;
	}

	&>a{
		display: block;
		text-decoration: none;
		color:inherit;

		transition:  .5s ease;

		&:focus,
		&:hover{
			outline: 0;
			.portfolio-img{
				img{
					opacity: .8;

					transform: scale(1.2);
				}
			}

			.portfolio-desc{
				background-color: $clr2;
				color:$clr1;

				border-top:1px solid $clr3;
			}
		}
	}
}

.portfolio-img {
	width: 100%;
	height: 235px;

	overflow: hidden;

	background: linear-gradient(110deg, rgb(51, 51, 51) 0%, rgb(231, 136, 53) 100%);
	
	img{
		width: 100%;
		height: 100%;
		display: block;
		object-fit: cover;

		transition: opacity .5s ease , transform 3s ease;
	}
}

.portfolio-desc {
	position: relative;

	min-height: 100px;

	padding: 15px;

	font-family: $roboto;
	color: $clr2;

	border-top:1px solid transparent;
	border-bottom: 1px solid $clr2;

	transition: color .5s ease, background-color .5s ease;

	&::before{
		content: '';
		display: block;

		width: 100%;

	}

	h3{
		margin: 0;
		margin-bottom: 10px;

		font-size: 16px;
		font-weight: bold;
		
	}

	p{
		margin: 0;
		margin-bottom: 5px;

		font-size: 13px;

		&:last-child{
			margin-bottom: 0;
		}
	}
}

.portfolio-link{
	@include default_btn($clr2,$clr1,$clr2);
}

@media(max-width: 1200px){
	.portfolio-list{
		justify-content: space-between;
	}

	.portfolio-item{
		max-width: 48%;
		margin-right: 0;

		&:nth-child(3n){
			margin-right: 0;
		}
	}

	.portfolio-desc{
		min-height: 0px;
	}
}

@media(max-width: 768px){
	.portfolio-list{
		flex-direction: column;
		align-items: center;

		margin-bottom: 30px;
	}

	.portfolio-item{
		max-width: 360px;
		min-height: auto;

		&:last-child{
			margin-bottom: 0;
		}
	}

	.portfolio-img{
		height: 180px;
	}
}